.dashboard {
  /* width: 100vw;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr 5fr;
  position: absolute; */
  display: flex;
  justify-content: space-between;
}

.dashboardContainer {
  width: 100vw;
  border-left: 1px solid rgba(0, 0, 0, 0.13);
  background-color: rgb(255, 255, 255);
  padding: 3rem 0;
}

.dashboardContainer > h1 {
  color: rgba(0, 0, 0, 0.733);
  font: 300 2rem "Roboto";
  text-align: center;
  width: 50%;
  padding: 1.5rem;
  margin: auto;
}

.dashboardSummary {
  margin: 2rem 0;
}

.dashboardSummary > div {
  display: flex;
  background-color: white;
  justify-content: center;
}
.dashboardSummary > div > p {
  background-color: rgba(70, 117, 218, 0.932);
  color: white;
  font: 300 1.3rem "Roboto";
  text-align: center;
  padding: 1.5rem;
  width: 100%;
  margin: 0 2rem;
}
.dashboardSummaryBox2 > a {
  color: rgb(0, 0, 0);
  font: 300 2rem "Roboto";
  text-align: center;
  background-color: rgb(255, 233, 174);
  text-decoration: none;
  padding: 1.5rem;
  width: 10vmax;
  height: 10vmax;
  margin: 2rem;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dashboardSummaryBox2 > a:first-child {
  background-color: rgb(255, 110, 110);
  color: rgb(255, 255, 255);
}

.dashboardSummaryBox2 > a:last-child {
  background-color: rgb(51, 51, 51);
  color: rgb(255, 255, 255);
}

.lineChart {
  width: 80%;
  margin: auto;
}

.doughnutChart {
  width: 30vmax;
  margin: auto;
}
.dashboardSummaryBox2 > a >p {
  font: 300 1.5rem "Roboto";
  margin-bottom: 0px;
}
@media (width <= 600px) {
  .dashboard {
    width: 100vw;
    flex-direction: column;
    gap: 20px;
    grid-template-columns: 1fr;
  }

  .dashboardSummary{
    padding: 10px;
    margin: 0;
  }
  .dashboardSummary > div > p {
    margin: 0;
  }

  .dashboardSummaryBox2 > a {
    margin: 1rem 0.5rem;
    font: 300 1rem "Roboto";
    width: 13vmax;
    height: 13vmax;
  }
  .dashboardSummary > div {
    display: flex;
    flex-wrap: wrap;
    background-color: white;
    justify-content: center;
  }
  .dashboardSummaryBox2 > a >p {
    font: 300 1.2rem "Roboto";
    margin-bottom: 0px;
  }
  .dashboardContainer > h1 {
    font: 300 1.5rem "Roboto";
    padding: 1.5rem 0rem;
    margin: auto;
    width: 100%;
  }
}
@media screen and ((width >= 600px) and (width <=1025px)) {
  .dashboard {
    grid-template-columns: 1fr;
  }

  .dashboardContainer {
    border-left: 0.px solid black;
  }

  .dashboardSummary{
    width: 67vw;
    padding: 10px;
    margin: auto;
  }
  .dashboardSummary > div > p {
    margin: 0;
  }

  .dashboardSummary > div {
    display: flex;
    background-color: white;
    justify-content: space-evenly;
  }
  .dashboardSummaryBox2 > a {
    margin: 1rem 0.5rem;
    font: 300 1rem "Roboto";
    width: 10vmax;
    height: 10vmax;
  }
  .dashboardSummaryBox2 > a >p {
    font: 300 0.8rem "Roboto";
    margin-bottom: 0px;
  }
  .dashboardContainer > h1 {
    font: 300 1.5rem "Roboto";
    padding: 1.5rem 0rem;
    margin-top: 0rem;
  }
}
